export function ValidarPositivo(valor) {
    if (valor < 0) {
        return alert("Cheque o valor inserido, apenas nmeros positivos so vlidos");
    } else {
        return valor;
    }
}

export function ValidarNumero(valor) {
    if (isNaN(valor)) {
        return alert("Por favor informar apenas dados numricos");
    } else {
        return valor;
    }
}